@charset "UTF-8";
.info__company {
  padding-inline-start: 395px;
  padding-inline-end: 350px;
  display: flex;
  gap: 50px;
  padding-block-start: 145px;
  padding-block-end: 70px;
}
@media (max-width: 1900px) {
  .info__company {
    padding-inline: 100px;
    justify-content: center;
  }
}
@media (max-width: 1250px) {
  .info__company {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
}
@media (max-width: 992.99px) {
  .info__company {
    padding-inline: 30px;
  }
}
@media (max-width: 600px) {
  .info__company {
    padding-block: 40px;
  }
}

.info__body-number {
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  font-size: 72px;
  line-height: 100%;
  padding-block-end: 5px;
}
@media (max-width: 1400px) {
  .info__body-number {
    font-size: 48px;
  }
}
@media (max-width: 600px) {
  .info__body-number {
    font-size: 34px;
  }
}

.info__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.info__body-text {
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  font-weight: 400;
  max-width: 100px;
  line-height: 20px;
}

.info__body-text--width {
  max-width: 350px;
}

.info__body-text--last {
  max-width: 150px;
}

.info__body--1 {
  margin-inline-start: 155px;
}
@media (max-width: 1250px) {
  .info__body--1 {
    margin: 0;
  }
}

.info__body--10 {
  margin-inline-start: 56px;
}
@media (max-width: 1250px) {
  .info__body--10 {
    margin: 0;
  }
}

.info__body--80 {
  position: relative;
  margin-inline-start: 30px;
}
.info__body--80::before {
  position: absolute;
  content: "%";
  top: 6px;
  right: 7px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  font-weight: 900;
}
@media (max-width: 600px) {
  .info__body--80::before {
    position: static;
    font-size: 8px;
  }
}
@media (max-width: 1250px) {
  .info__body--80 {
    margin: 0;
  }
}

.info__body--240,
.info__body--1,
.info__body--10 {
  position: relative;
}
.info__body--240::before,
.info__body--1::before,
.info__body--10::before {
  position: absolute;
  content: "Га";
  top: 0;
  right: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 100%;
  font-weight: 900;
}
@media (max-width: 600px) {
  .info__body--240::before,
  .info__body--1::before,
  .info__body--10::before {
    position: static;
    font-size: 8px;
  }
}

.info__body--240::before {
  top: 6px;
  right: -27px;
}

.info__body--1::before {
  top: 6px;
  right: 10px;
}

.info__body--10::before {
  top: 6px;
  right: 110px;
}